.hero {
  position: relative;

  padding-bottom: 40px;
  overflow: hidden;

  background-color: $gray-400;
  &__wrapper {
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-start;
    gap: 32px;
    @include screen(laptop) {
      flex-direction: row;
      gap: 40px;
    }
  }
  &__image {
    margin: 0 -16px;
    width: calc(100% + 32px);
    height: 100%;
    max-height: 350px;
    overflow: hidden;
    img {
      @include screen(laptop) {
        position: absolute;

        height: calc(100% - 140px);
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include screen(laptop) {
      max-width: 500px;
    }
  }
  &__title {
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 60px;
    font-family: $bebas;
    line-height: 1;
    span {
      color: $yellow;
    }
    br {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    @include screen(laptop) {
      padding-bottom: 24px;
    }
  }
  &__text {
    margin-top: 16px;

    font-size: 16px;
    font-family: $gilroy;
    line-height: 22px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    margin-top: 26px;
    width: 100%;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 40px;
    }
    @include screen(laptop) {
      margin-top: 60px;
    }
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    min-width: 32px;
    height: 32px;
    min-height: 32px;

    border: 2px solid $gray;
    svg {
      width: 12px;
      height: 12px;
      path {
        fill: $cyan;
      }
    }
  }
  &__item {
    display: flex;
    gap: 20px;
    p {
      padding-top: 8px;

      font-weight: 700;
      font-size: 20px;
      font-family: $bebas;
      line-height: 1;
      span {
        color: $cyan;
      }
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
      @include screen(laptop) {
        padding-top: 4px;
      }
    }
  }
  &__subtitle {
    margin-top: 24px;

    font-size: 20px;
    font-family: $bebas;
    line-height: 1;
  }
  &__clients {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 16px;
    @include screen(laptop) {
      gap: 24px;
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
    width: 100%;
  }
  &__links {
    display: flex;
    gap: 16px;
    a {
      border-bottom: 2px solid transparent;

      font-size: 16px;
      font-family: $gilroy;
      line-height: 1;
      color: $yellow;

      transition: border-color $transition-default;
    }
  }
  &__blog,
  &__more {
    border-bottom: 2px solid transparent;

    font-size: 16px;
    font-family: $gilroy;
    line-height: 18px;
    color: $cyan;

    transition: border-color $transition-default;
  }
  &__blog {
    &:hover {
      border-color: $yellow;
    }
  }
  &__more {
    &:hover {
      border-color: $cyan;
    }
  }
  @include screen(tablet) {
    padding-bottom: 70px;
  }
  @include screen(laptop) {
    padding-top: 70px;
  }
}