.about {
  padding: 40px 0;
  &__title {
    margin-bottom: 32px;

    font-weight: 700;
    font-size: 32px;
    line-height: 1;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 20px;
    width: 100%;
    @include screen(tablet) {
      gap: 40px;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 8px;
    height: 110px;
    overflow: hidden;

    border: 1px solid $gray;
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 16px;
    p {
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
    }
    &:nth-child(1) {
      img {
        width: 50%;
        max-height: 100px;
        object-fit: contain;
      }
    }
  }
  &__blocks {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 40px;
    margin-top: 40px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      margin-top: 48px;
    }
  }
  &__block {
    padding: 32px;
    min-height: 180px;

    border: 1px solid $gray;
    h3 {
      margin-bottom: 16px;

      font-weight: 700;
      font-size: 24px;
      font-family: $bebas;
      line-height: 1;
    }
    p {
      font-weight: 400;
      font-size: 16px;
      font-family: $gilroy;
      line-height: 20px;
    }
  }
  @include screen(laptop) {
    padding: 70px 0;
  }
}