.how {
  padding: 40px 0;
  &__content {
    position: relative;
    &:before,
    &:after {
      content: "";

      position: absolute;
      bottom: -40px;
      left: 50%;

      display: none;

      border: 20px solid transparent;
      border-top: 20px solid $white;
      transform: translateX(-50%);
      @include screen(tablet) {
        display: block;
      }
    }
    &:before {
      bottom: -40px;
      z-index: 2;
    }
    &:after {
      bottom: -41px;
      z-index: 1;

      border-top: 20px solid $gray;
    }
    h2 {
      position: relative;
      left: 50%;
      transform: translateX(-50%);

      display: inline-block;
      margin-bottom: 16px;

      font-weight: 700;
      font-size: 60px;
      font-family: $bebas;
      line-height: 60px;
      letter-spacing: 3px;
      @include screen(tablet) {
        font-size: 60px;
        line-height: 1;
        text-align: center;
        &:before,
        &:after {
          position: absolute;

          font-size: 60px;
          font-family: $bebas;
          line-height: 1;
          color: $gray;
        }
        &:before {
          content: "{";

          left: -40px;
        }
        &:after {
          content: "}";

          right: -40px;
        }
      }
    }
    h3 {
      font-weight: 700;
      font-size: 24px;
      font-family: $bebas;
      line-height: 1;
    }
    p {
      margin-top: 16px;

      font-size: 16px;
      font-family: $gilroy;
      line-height: 24px;
    }
    ol {
      list-style: decimal;

      padding-left: 32px;
      li {
        margin-top: 8px;

        font-size: 16px;
        font-family: $gilroy;
        line-height: 24px;
        &::marker {
          font-weight: 700;
          color: $yellow;
        }
      }
    }
    @include screen(tablet) {
      padding: 50px;

      border: 1px solid $gray;
      background-color: $white;
    }
  }
  @include screen(tablet) {
    background-color: $gray-400;
  }
  @include screen(laptop) {
    padding: 70px 0;
  }
}