.solutions {
  padding: 40px 0;
  &__title {
    position: relative;

    font-weight: 700;
    font-size: 50px;
    font-family: $bebas;
    line-height: 1;
    letter-spacing: 0.5px;
    br {
      display: block;
      @include screen(tablet) {
        display: none;
      }
    }
    svg {
      position: absolute;

      display: none;
      &:nth-child(1) {
        left: 8px;

        align-self: flex-start;
      }
      &:nth-child(3) {
        bottom: 30px;
        left: 290px;

        align-self: flex-end;
        @include screen(laptop) {
          bottom: auto;
          transform: translateX(30px);

          left: auto;
        }
      }
      @include screen(tablet) {
        display: inline-block;
      }
    }
    @include screen(tablet) {
      padding: 0 40px 0 54px;
    }
  }
  &__heading {
    position: relative;
    z-index: 2;

    padding-bottom: 30px;
    &:before {
      content: "Проблемы?";

      position: absolute;
      bottom: 62px;
      left: 50%;

      font-weight: 700;
      font-size: 80px;
      font-family: $bebas;
      line-height: 1;
      color: $gray-400;
      transform: translateX(-50%);
      @include screen(tablet) {
        bottom: -36px;
        left: 0;

        font-size: 200px;
        transform: translateX(0);
      }
      @include screen(laptop) {
        bottom: -50px;
      }
    }
  }
  &__items {
    display: grid;
    gap: 20px;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 40px;
    }
  }
  &__item {
    border: 5px solid $gray;
    border-bottom: none;
    img {
      width: 100%;
      object-fit: cover;
    }
    p {
      padding: 16px;

      background-color: $gray;

      font-weight: 700;
      font-size: 20px;
      font-family: $bebas;
      line-height: 24px;
      text-align: center;
      @include screen(laptop) {
        font-size: 26px;
        line-height: 32px;
      }
    }
  }
  @include screen(tablet) {
    padding: 132px 0 70px 0;
  }
}