.footer {
  padding: 54px 0 12px;

  background-color: $black;
  &__wrapper {
    @include screen(laptop) {
      padding-left: 340px;
    }
  }
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include screen(tablet) {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
    @include screen(laptop) {
      padding-right: 80px;
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 24px;
    margin-bottom: 16px;
    @include screen(tablet) {
      justify-content: flex-start;
    }
  }
  &__link {
    font-size: 18px;
    font-family: $bebas;
    line-height: 22px;
    color: $white;
    &-small {
      font-size: 14px;
      font-family: $gilroy;
      span {
        border-bottom: 1px solid $white;

        line-height: 16px;
      }
    }
  }
  &__icons {
    display: flex;
    gap: 24px;
    margin-top: 24px;
    @include screen(tablet) {
      margin-top: 0;
    }
  }
  &__copy {
    margin-top: 40px;

    font-size: 13px;
    font-family: $gilroy;
    line-height: 1;
    color: $white;
    @include screen(tablet) {
      margin-top: 100px;
    }
  }
}