.header {
  position: fixed;
  top: 0;
  z-index: 20;

  padding: 14px 0;
  width: 100%;

  background-color: $white;
  &__wrapper {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__container {
    display: flex;
    flex: 1;
    align-items: center;
    gap: 30px;
  }
  &__logo {
    max-width: 192px;
    img {
      width: 100%;
    }
  }
  &__menu {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    @include screen(tablet) {
      display: flex;
    }
  }
  &__slogan {
    font-size: 26px;
    font-family: $bebas;
    line-height: 1;
    br {
      display: block;
      @include screen(laptop) {
        display: none;
      }
    }
  }
  &__nav {
    display: none;
    gap: 22px;
    @include screen(laptop) {
      display: flex;
    }
  }
  &__link {
    border-bottom: 2px solid transparent;

    font-size: 18px;
    font-family: $bebas;
    line-height: 1;

    transition: color $transition-default, border-color $transition-default;
    &:hover {
      border-color: $cyan;

      color: $cyan;
    }
  }
  &__contact {
    display: none;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    button {
      padding: 9px 24px 8px;

      border: 2px solid $cyan;

      font-size: 14px;
      font-family: $gilroy;
      line-height: 1;
      text-transform: uppercase;
      color: $cyan;

      transition: color $transition-default,
        background-color $transition-default;
      &:hover {
        background-color: $cyan;

        color: $white;
      }
    }
    @include screen(laptop) {
      display: flex;
    }
  }
  &__phone {
    font-weight: 700;
    font-size: 30px;
    font-family: $bebas;
    line-height: 1;

    transition: color $transition-default;
    &:hover {
      color: $cyan;
    }
  }
  &__burger {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    padding: 16px;

    border: 1px solid $gray;
    span {
      width: 20px;
      height: 2px;

      background-color: $cyan;

      transition: opacity $transition-default;
    }
    &.active {
      span {
        &:nth-child(1) {
          transform: rotate(45deg) translate(5px, 5px);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:nth-child(3) {
          transform: rotate(-45deg) translate(4px, -3px);
        }
      }
    }
    @include screen(laptop) {
      display: none;
    }
  }
  @include screen(tablet) {
    padding: 27px 0;
  }
  @include screen(laptop) {
    padding: 22px 0;
  }
}

.mobile-menu {
  position: fixed;
  top: 90px;
  z-index: 20;

  width: 100%;

  background-color: $gray-400;
  &__wrapper {
    display: none;
    &.active {
      display: block;
    }
  }
  &__slogan {
    position: relative;

    padding: 16px 0;

    background-color: $white;

    font-weight: 700;
    font-size: 20px;
    font-family: $bebas;
    line-height: 24px;
    letter-spacing: 0.2px;
    &:before,
    &:after {
      content: "";

      position: absolute;
      top: 0;

      width: 999px;
      height: 100%;

      background-color: $white;
    }
    &:before {
      left: -999px;
    }
    &:after {
      right: -999px;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 32px 0;
    a {
      font-weight: 700;
      font-size: 20px;
      font-family: $bebas;
      line-height: 1;
    }
  }
  &__footer {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 25px 0;

    background-color: $cyan-400;
    &:before,
    &:after {
      content: "";

      position: absolute;
      top: 0;

      width: 999px;
      height: 100%;

      background-color: $cyan-400;
    }
    &:before {
      left: -999px;
    }
    &:after {
      right: -999px;
    }
  }
  &__phone {
    font-weight: 700;
    font-size: 30px;
    font-family: $bebas;
    line-height: 1;
    letter-spacing: 1px;
    color: $white;
  }
  &__button {
    padding: 8px 40px;

    border: 1px solid $white;

    font-weight: 700;
    font-size: 14px;
    font-family: $gilroy;
    line-height: 1;
    text-transform: uppercase;
    color: $white;
  }
  @include screen(laptop) {
    display: none;
  }
}