.contacts {
  background-color: $cyan-400;
  &__wrapper {
    position: relative;

    display: grid;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      gap: 62px;
    }
  }
  &__logo {
    position: absolute;
    bottom: -120px;

    display: none;

    font-weight: 700;
    font-size: 148px;
    line-height: 1;
    color: $cyan-400;
    transform: rotate(-90deg);
    span {
      color: $black;
    }
    @include screen(laptop) {
      display: block;
    }
  }
  &__image {
    position: relative;

    display: none;
    justify-content: flex-end;
    img {
      position: absolute;
      right: 0;
      bottom: 0;

      min-width: 452px;
      @include screen(laptop) {
        right: -100px;

        min-width: auto;
      }
    }
    @include screen(tablet) {
      display: flex;
    }
  }
  &__content {
    position: relative;
    z-index: 2;

    padding: 40px 0;
    @include screen(laptop) {
      padding: 70px 80px 48px 0;
    }
  }
  &__title {
    margin-bottom: 10px;

    font-weight: 700;
    font-size: 50px;
    font-family: $bebas;
    line-height: 1;
    color: $white;
  }
  &__description {
    margin-bottom: 20px;

    font-size: 16px;
    font-family: $gilroy;
    line-height: 21px;
    color: $white;
  }
  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__label {
    display: block;
    width: 100%;
  }
  &__input,
  &__textarea {
    padding: 12px 0;
    width: 100%;

    border: none;
    border-bottom: 1px solid $white;
    border-radius: 0;
    background-color: transparent;

    font-family: $gilroy;
    color: $white;
    resize: none;
    &::placeholder {
      color: $white;
    }
  }
  &__textarea {
    margin-top: 40px;
    padding: 16px;
    height: 125px;

    border: 1px solid $white;
  }
  &__policy {
    position: relative;

    margin-top: 30px;

    color: $white;
    input {
      position: absolute;
      opacity: 0;

      width: 0;
      height: 0;
      &:checked {
        & + span {
          &:after {
            opacity: 1;
          }
        }
      }
    }
    span {
      cursor: pointer;
      &:before {
        content: "";

        position: absolute;

        width: 24px;
        height: 24px;

        border: 1px solid $white;
      }
      &:after {
        content: "";

        position: absolute;
        top: 4px;
        left: 4px;

        width: 16px;
        height: 16px;

        background-color: $white;

        transition: opacity $transition-default;
        opacity: 0;
      }
    }
    p {
      padding-top: 2px;
      padding-left: 40px;

      font-size: 14px;
      font-family: $gilroy;
      line-height: 20px;
      a {
        border-bottom: 1px solid $white;

        font-family: $gilroy;
      }
    }
  }
  &__button {
    margin-top: 40px;
    padding: 16px 44px;
    width: 100%;

    border: 1px solid $white;

    font-size: 14px;
    font-family: $gilroy;
    line-height: 1;
    color: $white;

    transition: color $transition-default, background-color $transition-default;
    &:hover {
      background-color: $white;

      color: $cyan-400;
    }
    @include screen(tablet) {
      width: auto;
    }
  }
}