.rates {
  padding-bottom: 40px;
  &__title {
    margin-bottom: 20px;

    font-weight: 700;
    font-size: 30px;
    font-family: $bebas;
    line-height: 1;
    @include screen(tablet) {
      margin-bottom: 30px;

      text-align: center;
    }
  }
  &-header {
    display: none;
    &__item {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;

      background-color: $gray;

      font-weight: 700;
      font-size: 18px;
      font-family: $bebas;
      line-height: 24px;
      text-align: center;
      color: $white;
    }
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &-body {
    display: none;

    border: 1px solid $gray;
    &__item {
      display: grid;
      grid-template-rows: repeat(3, minmax(0, 1fr));

      border-right: 1px solid $gray;

      font-family: $gilroy;
      &:last-child {
        border-right: none;
      }
      p {
        font-size: 16px;
        font-family: $gilroy;
        line-height: 24px;
      }
      ol {
        list-style: decimal;

        padding-left: 32px;
        li {
          margin-top: 8px;

          font-size: 16px;
          font-family: $gilroy;
          line-height: 24px;
          &::marker {
            font-weight: 700;
            color: $yellow;
          }
        }
      }
    }
    &__block {
      padding: 20px;

      border-bottom: 1px solid $gray;
      &:last-child {
        border-bottom: none;
      }
    }
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &-footer {
    &__block {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 12px;
      padding: 32px 0;
      &:nth-child(2n + 1) {
        background-color: $gray-400;
        @include screen(tablet) {
          background-color: transparent;
        }
      }
      h3 {
        font-size: 16px;
        font-family: $gilroy;
        line-height: 24px;
        text-align: center;
      }
      p {
        font-weight: 700;
        font-size: 16px;
        letter-spacing: 2px;
        color: $yellow;
        span {
          font-size: 32px;
        }
        i {
          font-style: normal;
          font-family: $gilroy;
        }
      }
      @include screen(tablet) {
        padding: 0;
      }
    }
    &__button {
      border-bottom: 2px solid transparent;

      font-weight: 700;
      font-size: 14px;
      font-family: $gilroy;
      line-height: 18px;
      text-transform: uppercase;
      color: $cyan;
      &-example {
        transition: border-color $transition-default;
        &:hover {
          border-color: $cyan;
        }
      }
      &-order {
        padding: 9px 40px 8px;

        border: 2px solid $cyan;

        transition: color $transition-default, border-color $transition-default,
          background-color $transition-default;
        &:hover {
          border-color: $cyan;
          background-color: $cyan;

          color: $white;
        }
      }
    }
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      margin-top: 32px;
    }
  }
  @include screen(tablet) {
    padding-bottom: 70px;

    background-color: $gray-400;
  }
}