.reviews {
  padding: 40px 0;
  &__wrapper {
    position: relative;
    .swiper-slide {
      &:hover {
        .reviews__body {
          a {
            opacity: 1;
          }
        }
      }
    }
    .swiper-button {
      &-prev,
      &-next {
        margin-top: 0;
        &:after,
        &:before {
          font-size: 20px;
        }
      }
      &-prev {
        left: -40px;
      }
      &-next {
        right: -40px;
      }
    }
  }
  &__items {
    margin-top: 40px;
  }
  &__title {
    position: relative;

    display: flex;
    align-items: center;
    h2 {
      position: relative;

      display: inline-block;
      margin-bottom: 16px;

      font-weight: 700;
      font-size: 60px;
      font-family: $bebas;
      line-height: 60px;
      letter-spacing: 3px;
      @include screen(tablet) {
        left: 50%;
        transform: translateX(-50%);

        font-size: 60px;
        line-height: 1;
        text-align: center;
        &:before,
        &:after {
          position: absolute;

          font-size: 60px;
          font-family: $bebas;
          line-height: 1;
          color: $gray;
        }
        &:before {
          content: "{";

          left: -40px;
        }
        &:after {
          content: "}";

          right: -40px;
        }
      }
    }
    a {
      position: absolute;
      right: 0;

      border-bottom: 2px solid $cyan;

      font-weight: 700;
      font-size: 14px;
      font-family: $gilroy;
      line-height: 18px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $cyan;
    }
  }
  &__header {
    position: relative;

    margin-left: 40px;
    padding-left: 20px;

    border-left: 2px solid $gray;
    svg {
      position: absolute;
      top: 0;
      left: -40px;
    }
    h3 {
      margin-bottom: 3px;

      font-weight: 700;
      font-size: 20px;
      font-family: $bebas;
      line-height: 1;
    }
    span {
      font-size: 13px;
      font-family: $gilroy;
      line-height: 13px;
    }
  }
  &__body {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 20px;
    p {
      font-size: 14px;
      font-family: $gilroy;
      line-height: 21px;
    }
    a {
      display: inline-block;
      margin-top: 16px;

      border-bottom: 2px solid $cyan;

      font-weight: 700;
      font-size: 14px;
      font-family: $gilroy;
      line-height: 18px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $cyan;

      transition: opacity $transition-default;
      opacity: 0;
    }
  }
  @include screen(laptop) {
    padding: 70px 0;
  }
}